import { Component, OnInit, Output, Renderer2 } from '@angular/core';
import { GameService } from '../../services/game.service';
import { Message } from '../../models/message.model';

@Component({
  selector: 'app-inventory-profile',
  templateUrl: './inventory-profile.component.html',
  styleUrls: ['./inventory-profile.component.css']
})
export class InventoryProfileComponent implements OnInit {

  items: string[];
  equipment: {weapon, headGear, bodyArmor, shield};

  constructor(private gameService: GameService,
              private renderer:Renderer2) { }

  ngOnInit() {
    this.items = this.gameService.player.items;
    this.equipment = this.gameService.player.equipment;

    this.gameService.playerSubject.subscribe((m)=> {
      if(m.type == "equipment" && m.event == "change")
      {
        this.gameService.player.items = m.data.items;
        this.gameService.player.equipment = m.data.equipment;
        this.items = m.data.items;
        this.equipment = m.data.equipment;
      }
    })
  }

  drag($event) {
    let j;
    if($event.originalTarget && $event.originalTarget instanceof HTMLElement && $event.originalTarget.getAttribute('iid'))
      j = JSON.stringify({iid: $event.originalTarget.getAttribute('iid'), id: $event.originalTarget.id});
      else
      j = JSON.stringify({iid: -1, id: -1});
    $event.dataTransfer.setData("text", j);
  }

  drop($event) {

    let obj: {id: string, iid: string} = JSON.parse($event.dataTransfer.getData("text"));
    if(obj.id != '-1')
      {

        $event.preventDefault();
        $event.dataTransfer.dropEffect = "move";
        
        var data = document.getElementById(obj.id);
        var el: HTMLElement = $event.currentTarget;

        this.gameService.playerSubject.next(new Message('equipment', 'change', {id: obj.iid, index: data.getAttribute('ind')}));
      }
  }

  allowDrop($event) {
    $event.preventDefault();
   
  }

  enterDrop($event) {
    this.renderer.setStyle($event.target, 'box-shadow', '0px 0px 5px 1px blue'); 
  }

  leaveDrop($event) {
    $event.preventDefault();
    this.renderer.setStyle($event.target, 'box-shadow', 'inset 0px 0px 0px 0px blue'); 
  }

  //////INV


  dropInv($event) {
    console.log($event.dataTransfer.getData("text").charAt(0));

    if($event.dataTransfer.items.length == 1 && $event.dataTransfer.getData("text").charAt(0) == '{')
      {
        let obj: {id: string, iid: string} = JSON.parse($event.dataTransfer.getData("text"));
        console.log(obj.id);
        if(obj.id != '-1')
          {
            
            $event.preventDefault();
            $event.dataTransfer.dropEffect = "move";
    
            var data = document.getElementById(obj.id);
            var el: HTMLElement = $event.currentTarget;
    
            this.gameService.playerSubject.next(new Message('equipment', 'unequip', {id: obj.iid, equipto: obj.id}));
           
          }
}
  }

  enterDropInv($event) {
    this.renderer.setStyle($event.target, 'background-color', 'gray'); 
  }

  leaveDropInv($event) {
    $event.preventDefault();
    this.renderer.setStyle($event.target, 'background-color', 'transparent'); 
  }

}
