export class Player {
    health: number = 100;
    maxhealth: number = 100;
    isAlive: boolean = true;
    money: number = 0;
    level: number = 0;
    experience: number = 0;
    experienceToLevelUp: number = 0;
    mana: number = 20;
    maxmana: number = 20;
    damage = {min:0 ,max: 0};
    defence = {soft: 0, hard: 0};


    items = [];
    equipment = {weapon: undefined,
        headGear: undefined,
        bodyArmor: undefined,
        shield: undefined}

    updateHealthPoints(health: {amount: number, max: number}) {
        this.health = health.amount;
        this.maxhealth = health.max;
        if(this.health <= 0) {
            this.isAlive = false;
            this.health = 0;
        }
            
        else
            this.isAlive = true;
    }

    updateManaPoints(mana: {amount: number, max: number}) {
        this.mana = mana.amount;
        this.maxmana = mana.max;
    }

    alive(): boolean {
        return this.isAlive;
    }

    static createFromServerType(playerObj) {
        let p = new Player();
        p.updateFromServerType(playerObj);
        return p;
    }

    updateFromServerType(playerObj) {
        this.health = playerObj.health;
        this.maxhealth = playerObj.healthPoolCap;
        this.mana = playerObj.mana;
        this.maxmana = playerObj.manaPoolCap;
        this.money = playerObj.money;
        this.level = playerObj.level;
        this.experience = playerObj.experience;
        this.experienceToLevelUp = playerObj.experienceToLevelUp;
        this.defence = playerObj.defence;
        this.damage = playerObj.damage;
        this.items = playerObj.items;
        this.equipment = playerObj.equipment;
    }
}