import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { GameService } from '../../services/game.service';
import { Message } from '../../models/message.model';

@Component({
  selector: 'app-info',
  templateUrl: './info.component.html',
  styleUrls: ['./info.component.css']
})
export class InfoComponent implements OnInit {

  @ViewChild('inmsg') inputMsgRef: ElementRef;
  messages: string[] = [];
  messageSubject;

  constructor(private gameService: GameService) { }

  ngOnInit() {
    this.messageSubject = this.gameService.messageSubject;
    this.messageSubject.subscribe((data) => {
      this.messages.push(data.data);
    });
  }

  
  sendMessage() {
    this.messageSubject.next(new Message('new-message','chat','[Chat]' + this.inputMsgRef.nativeElement.value));
  }

}
