import { Component, OnInit } from '@angular/core';
import { GameService } from '../../services/game.service';
import { Message } from '../../models/message.model';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  constructor(private gameService: GameService) { }

  ngOnInit() {
  }

  charSelect(char: string) { // CALLED ONLY ONCE, THEN BUTTON DISSAPEARS
    let playerSubject = this.gameService.playerSubject;

    playerSubject.subscribe((m)=> {
      switch(m.type) {
        case "character":
          switch(m.event) {
            case "chosen":
            this.gameService.charImage = m.data+".png";
            this.gameService.charChosen = true;
            break;
          }
        break;
      }
    });

    playerSubject.next(new Message('character', 'chosen',char));
    
  }

}
