import { Injectable, EventEmitter } from "@angular/core";
import { Subject } from 'rxjs';
import 'rxjs/add/operator/map';
import { WebSocketService } from './websocket.service';
import { Message } from '../models/message.model';
import { Player } from '../models/player.model';

enum menu {
    home = -1,
    inv_prof = 0,
    battle = 1,
    stats = 2,
    floors = 3,
    about = 4,
    info = 5
  }

@Injectable()
export class GameService {
    onSelectedMenuEvent = new EventEmitter<number>();

    //////////////////////////////
    ////// Shared Subjects ///////
    //////////////////////////////
    messageSubject: Subject<any>;
    playerSubject: Subject<any>;
    battleSubject: Subject<any>;
    gameSubject: Subject<any>;
    //////////////////////////////

    connected: boolean;
    loadingStage: number = 0;
    endStage: number = 5;

    menu = menu;
    selectedMenu = menu.home;

    charChosen = false;

    msg: string;

    //need a player obj for this. // GAME OBJECT
    player: Player = new Player();
    charImage = "merlin.png";
    floors: any[]; //need to create floor object/model
    highestFloorReached = 0;

    attacks: any[];
    defences: any[];


    constructor(private webSocketService: WebSocketService) {
       webSocketService.connect(); 
        
       //INIT SUBJECTs
       this.battleSubject = this.on('battle');
       this.messageSubject = this.on('message');
       this.playerSubject = this.on('player');
       this.gameSubject = this.on('game');

       this.on('connect').subscribe(() => {
        this.connected = true;
        this.loadingStage++;
       
        this.playerSubject.subscribe((m) => {
            if(m.type == "stats" && m.event == "all") {
                this.player.updateFromServerType(m.data);
                this.loadingStage++;
            }else
            if(m.type == "floors" && m.event == "update")
            {
                this.highestFloorReached = m.data;
            } else
            if(m.type == "floors" && m.event == "change")
            {
                this.msg = "FLOOR CHANGED";
                setTimeout(() => {
                    this.msg = undefined;
                }, 3500);
            }
        });

        //get game data
        this.gameSubject.subscribe((m) => {
            console.log(m.type);
            switch(m.type) {
                case 'floors':
                switch(m.event) {
                    case 'get':
                    this.loadingStage++;
                    this.floors = m.data;
                    break;
                }
                break;
                case 'attacks':
                switch(m.event) {
                    case 'get':
                    this.loadingStage++;
                    this.attacks = m.data;
                    break;
                }
                break;
                case 'defences':
                switch(m.event) {
                    case 'get':
                    this.loadingStage++;
                    this.defences = m.data;
                    break;
                }
                break;
            }
        });

        this.gameSubject.next(new Message('floors','get',''));
        this.gameSubject.next(new Message('attacks','get',''));
        this.gameSubject.next(new Message('defences','get',''));
        this.playerSubject.next(new Message('stats','all',''));

       });



    }



    on(event: string): Subject<any> {
        return this.webSocketService.on(event);
    }

    send(event, message: Message) {
        this.webSocketService.send(event, message);
    }


    onSelectedMenu(menu) {
        this.selectedMenu = menu;
        this.onSelectedMenuEvent.emit(menu);
    }

}